import "./About.css";

const About = () => {
  return (
    <section id="about" className="about-section">
      <div className="about-title-container">
        <h1 className="about-heading add-gradient">About</h1>
        <div className="about-divider" />
      </div>
      <div className="about-content row">
        <div className="col-sm-8">
          <p className="about-content">
            Hey! I'm Nishanth, a Full Stack Developer based in Chennai. I have a
            passion for coding and get most enjoyment from solving problems with
            writing elegant/maintainable code. I love learning so I'm always
            keeping up to date by watching new tech news and new tutorials on
            youtube. My interest in programming started back in 2017 when I
            started to learn Python language.
          </p>
        </div>
        <div className="col-sm-4 about-pic-container">
          <img
            className="about-pic"
            src="https://avatars.githubusercontent.com/u/63568590?v=4"
            alt="profile-pic"
          />
        </div>
      </div>
    </section>
  );
};

export default About;
