import { skills } from "../../db";
import "./Skills.css";

const Skills = () => {
  return (
    <section id="skills" className="skill-section">
      <div className="skill-title-container">
        <h1 className="skill-heading add-gradient">Skills</h1>
        <div className="skill-divider" />
      </div>
      <p>I mostly work on...</p>
      <div className="row">
        {skills.map((item) => (
          <div
            key={item}
            className="col-6 col-sm-3 skill-container gx-1 gy-1 add-gradient"
          >
            {item}
          </div>
        ))}
      </div>
    </section>
  );
};

export default Skills;
