import { useEffect } from "react";

import "./Sidebar.css";

const Sidebar = ({ show, close }) => {
  useEffect(() => {
    if (show) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "unset";
    }
  }, [show]);

  return (
    <>
      {show && (
        <div className="offcanvas-backdrop" onClick={() => close(false)}></div>
      )}
      <div className={`offcanvas offcanvas-end ${show && "show"}`}>
        <ul className="offcanvas-list">
          <li className="offcanvas-item">
            <a className="nav-link" href="#about" onClick={() => close(false)}>
              About
            </a>
          </li>
          <li className="offcanvas-item">
            <a className="nav-link" href="#skills" onClick={() => close(false)}>
              Skills
            </a>
          </li>
          <li className="offcanvas-item">
            <a
              className="nav-link"
              href="#project"
              onClick={() => close(false)}
            >
              Projects
            </a>
          </li>
          <li className="offcanvas-item">
            <a
              className="nav-link"
              href="#contact"
              onClick={() => close(false)}
            >
              Contact
            </a>
          </li>
          <li className="offcanvas-item">
            <a
              className="nav-link"
              href="https://drive.google.com/file/d/1sipXIqMM1pIdrH1h4PqkDYiRUWKg5Rxa/view"
              target="_blank"
              rel="noreferrer"
              onClick={() => close(false)}
            >
              Resume
            </a>
          </li>
        </ul>
        <button className="offcanvas-btn" onClick={() => close(false)}>
          <svg height="20px" width="20px" viewBox="0 0 32 32">
            <path
              style={{ fill: "#d4d4d4" }}
              d="M4,10h24c1.104,0,2-0.896,2-2s-0.896-2-2-2H4C2.896,6,2,6.896,2,8S2.896,10,4,10z M28,14H4c-1.104,0-2,0.896-2,2  s0.896,2,2,2h24c1.104,0,2-0.896,2-2S29.104,14,28,14z M28,22H4c-1.104,0-2,0.896-2,2s0.896,2,2,2h24c1.104,0,2-0.896,2-2  S29.104,22,28,22z"
            />
          </svg>
        </button>
      </div>
    </>
  );
};

export default Sidebar;
