import { useState } from "react";

import Sidebar from "../Sidebar/Sidebar";

import "./Header.css";

const Header = () => {
  const [toggleCanvas, setToggleCanvas] = useState(false);

  const toggleSidebar = () => {
    setToggleCanvas(!toggleCanvas);
  };

  return (
    <header className="header">
      <nav className="nav">
        <a href="#intro" className="nav-brand">
          iamNishanth
        </a>
        <ul className="nav-links">
          <li className="nav-items">
            <a className="nav-link" href="#about">
              About
            </a>
          </li>
          <li className="nav-items">
            <a className="nav-link" href="#skills">
              Skills
            </a>
          </li>
          <li className="nav-items">
            <a className="nav-link" href="#project">
              Projects
            </a>
          </li>
          <li className="nav-items">
            <a className="nav-link" href="#contact">
              Contact
            </a>
          </li>
          <li className="nav-items">
            <a
              className="nav-link"
              href="https://drive.google.com/file/d/1sipXIqMM1pIdrH1h4PqkDYiRUWKg5Rxa/view"
              target="_blank"
              rel="noreferrer"
            >
              Resume
            </a>
          </li>
          <button className="nav-toggle" onClick={toggleSidebar}>
            <svg height="20px" width="20px" viewBox="0 0 32 32">
              <path
                style={{ fill: "#d4d4d4" }}
                d="M4,10h24c1.104,0,2-0.896,2-2s-0.896-2-2-2H4C2.896,6,2,6.896,2,8S2.896,10,4,10z M28,14H4c-1.104,0-2,0.896-2,2  s0.896,2,2,2h24c1.104,0,2-0.896,2-2S29.104,14,28,14z M28,22H4c-1.104,0-2,0.896-2,2s0.896,2,2,2h24c1.104,0,2-0.896,2-2  S29.104,22,28,22z"
              />
            </svg>
          </button>
        </ul>
      </nav>
      <Sidebar show={toggleCanvas} close={setToggleCanvas} />
    </header>
  );
};

export default Header;
