import { External, Folder, Github } from "../Icons";

import "./ProjectCard.css";

const ProjectCard = ({ project }) => {
  return (
    <div className="project-card">
      <header className="project-card-top">
        <Folder size="32" />
        <div className="project-card-top-right">
          <a
            className="project-url"
            href={project.github}
            target="_blank"
            rel="noreferrer"
          >
            <Github size="20" />
          </a>
          <a
            className="project-url"
            href={project.deploy}
            target="_blank"
            rel="noreferrer"
          >
            <External size="20" />
          </a>
        </div>
      </header>
      <div className="project-card-middle">
        <h1 className="project-name add-gradient">{project.name}</h1>
        <p className="project-description">{project.description}</p>
      </div>
      <footer className="project-card-bottom">
        <ul>
          {project.tech.map((item) => (
            <li key={item} className="project-tech">
              {item}
            </li>
          ))}
        </ul>
      </footer>
    </div>
  );
};

export default ProjectCard;
